import { Container } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { getUserInfo } from '../../../../../actions/loginActions';
import { SHOPPABLE_TOGGLE_API } from '../../../../../actions/api';
import { useApiRequest } from '../../../../../hooks/useApiRequest';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';

export default function Shopable({ userData, allWalls, hideModal = null }) {
    const { request: toogleShopable } = useApiRequest();
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const toggleAction = () => {
        toogleShopable({
            method: 'POST',
            url: SHOPPABLE_TOGGLE_API,
            body: {
                shoppableStatus: userData?.UserRule?.shoppableStatus ? 0 : 1,
                wallId: allWalls?.map(wall=> wall.id) ?? []
            },
            onSuccess: () => {
                if(hideModal) hideModal();
                dispatch(getUserInfo(navigate, "/home"))
            }
        });
    }

    return <Container fluid>
        <div className='f-center' style={{ minHeight: 'calc(100vh - 300px)' }}>
            <EmptyWithIcon title="Shoppable" desc={hideModal ? 'Turn your content into conversions. Enable Shoppable & tag products to drive sales directly from your UGC Widget.' : 'Dummy content API access with our upgraded plan to integrate Tagshop gallery in your application and make your app stand out.'} icon={'shoppable'} button={{ action: toggleAction, text: hideModal ? 'Enable Now' : userData?.UserRule?.shoppableStatus ? 'Disable' : 'Enable' }} isFullScreen={false} />
        </div>
    </Container>
}
